.modal-container {
  display: none;
}
.show-modal-container {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(22, 8, 8, 0.8);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 80%;
  max-height: 700px;
  max-height: 800px;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  background-color: #4d4d4d;
}
.modal-content h2 {
  flex: 0 0 60px;
  display: flex;
  align-items: center;
  margin-bottom: 0.25rem;
  padding: 0.125rem 0.8rem;
  font-size: 1.5rem;
  background-color: #000;
  color: yellow;
}
.modal-items-list {
  flex: 1;
  display: flex;
  margin-bottom: 0.25rem;
  padding: 0.5rem;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.modal-item-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0.125rem;
}
.modal-item-details {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 0.3rem;
  padding: 0.2rem;
  overflow: hidden;
  margin-bottom: 0.25rem;
  /* border: 1px solid red; */
}

.modal-menuItem {
  grid-column: 1 / 3;
  grid-row: 1 / 6;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.25rem;
  background-color: #b0b0b0;
  /* border: 1px solid red; */
}
.modal-menuItem img {
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
}
.modal-info {
  grid-column: 3 / 6;
  grid-row: 1 / 2;
  display: flex;
  flex-direction: column;
  border-radius: 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  /* border: 1px solid blue; */
}
.modal-item-title {
  /* flex: 1; */
  flex: 0 1 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* margin-bottom: 0.25rem; */
  padding: 0.15rem;
  /* border: 1px solid red; */
}
.modal-item-title h3 {
  padding: 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 1.3rem;
  /* border: 1px solid red; */
}
.modal-item-title span {
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  /* border: 1px solid red; */
}
.drink-type {
  flex: 0 0 40%;
  /* flex: 1; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-bottom: 0.25rem; */
  /* padding: 0.25rem; */
  padding: 0.1rem;
  /* padding: 0; */
  overflow: hidden;
  /* border: 1px solid red; */
}
.drink-type .hot-type,
.drink-type .iced-type {
  margin: 0.5rem;
  /* width: 60px; */
  /* height: 100%; */
  padding: 0.3rem 0.7rem;
  width: auto;
  height: auto;
  background-color: transparent;
  border-color: transparent;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 0.05rem;
  border-radius: 0.5rem;
  color: #b0b0b0;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
}
.hot-type {
  /* border: 2px solid rgba(255, 0, 0, 0.5); */
  border: 2px solid #b0b0b0;
}
.iced-type {
  /* border: 2px solid rgba(0, 0, 255, 0.5); */
  border: 2px solid #b0b0b0;
}
.hot-type:hover {
  color: red;
  /* border: 2px solid rgba(255, 0, 0, 0.8); */
}
.iced-type:hover {
  color: blue;
  /* border: 2px solid rgba(0, 0, 255, 0.8); */
}
.active-hot.hot-type {
  color: red;
  /* border: 2px solid rgba(255, 0, 0, 0.8); */
  background-color: #b0b0b0;
}
.active-iced.iced-type {
  color: blue;
  /* border: 2px solid rgba(0, 0, 255, 0.8); */
  background-color: #b0b0b0;
}

.selected-items-info {
  grid-column: 3 /6;
  grid-row: 2 / 6;
  overflow: auto;
  /* padding: 0.1rem; */
  border-radius: 0.25rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  /* border: 1px solid red; */
}
.items-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.2rem;
  margin: 0.2rem;
  /* border: 1px solid blue; */
}
.size-selection-row,
.quantity-selection-row,
.coffee-row {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.25rem;
  margin-bottom: 0.25rem;
  font-weight: bold;
  /* border: 1px solid green; */
}
.size-selection-row h4,
.quantity-selection-row h4,
.coffee-row h4 {
  font-size: 1rem;
  margin: 0.25rem 0;
  padding: 0.25rem;
  color: #fff;
  line-height: 1;
  /* border: 1px solid red; */
}
.btn-row {
  width: 100%;
  padding: 0.25rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* border: 1px solid yellow; */
}
.btn-row .coffee-size {
  flex: 1;
  margin: 0 0.2rem;
  padding: 0.25rem 0.5rem;
  background-color: transparent;
  border-color: transparent;
  color: #b0b0b0;
  font-size: 1rem;
  text-transform: capitalize;
  cursor: pointer;
}
.btn-row .active-size {
  color: #fff;
}
.btn-row .minus,
.btn-row .plus {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.1rem;
  font-size: 1rem;
  background-color: transparent;
  border-color: transparent;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  /* border: 1px solid red; */
}
.btn-row span {
  font-size: 1rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;

  /* border: 1px solid red; */
}
.coffee-row-header p {
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.25rem 0;
  padding: 0.25rem;
  color: #fff;
  line-height: 1;
  /* border: 1px solid red; */
}

.coffee-row-header {
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid blue; */
}
.item-total {
  flex: 0 0 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  color: #fff;
  font-weight: bold;
  letter-spacing: 0.07rem;
}

.order-btn {
  flex: 0 0 40px;
  display: flex;
  margin: 0 0.125rem 0.25rem;
}

@media screen and (max-width: 479px) {
  .modal-content {
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    transform: translateY(90px);
    max-height: calc(100% - 90px);
    width: 100%;
    height: 100%;
    padding: 0.25rem;
  }
  .modal-content h2 {
    flex: 0 0 60px;
    margin-bottom: 0.25rem;
    padding: 0.125rem 0.2rem;
    padding-left: 0.5rem;
    font-size: 1.3rem;
  }
  .modal-items-list {
    margin-bottom: 0.25rem;
    padding: 0.25rem;
    /* border: 1px solid red; */
  }
  .modal-item-container {
    /* padding: 0.125rem; */
    padding: 0;
    /* border: 1px solid red; */
  }
  .modal-item-details {
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 0.25rem;
    margin-bottom: 0.25rem;
    /* border: 1px solid red; */
  }
  .modal-menuItem {
    grid-column: 1 / 3;
    grid-row: 1 / 3;
    border: 1px solid yellow;
  }
  .modal-info {
    grid-column: 3 / 6;
    grid-row: 1 / 3;
    border: 1px solid yellow;
    /* overflow: hidden; */
  }
  .modal-item-title {
    margin-bottom: 0.15rem;
    padding: 0.2rem;
    border: 1px solid red;
  }
  .modal-item-title h3 {
    padding: 0.1rem;
    font-size: 1rem;
    /* border: 1px solid red; */
  }
  .modal-item-title span {
    padding: 0.15rem;
  }

  .drink-type {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 0.25rem;
    padding: 0;
    /* padding: 0.5rem; */
    padding: 0.25rem;
    overflow: hidden;
    /* border: 1px solid red; */
  }

  .drink-type .hot-type,
  .drink-type .iced-type {
    /* width: 50px; */
    margin: 0.5rem;
    padding: 0.35rem 0.75rem;
    width: auto;
    height: auto;
    font-size: 0.8rem;
    letter-spacing: 0.05rem;
  }

  .selected-items-info {
    grid-column: 1 /6;
    grid-row: 3 / 6;
    border: 1px solid yellow;
  }
  .items-info {
    padding: 0.2rem;
    margin: 0.2rem;
    /* border: 1px solid blue; */
  }
  .size-selection-row,
  .quantity-selection-row,
  .coffee-row {
    margin-bottom: 0.25rem;
    padding: 0.1rem;
    border-bottom: 1px solid rgba(176, 176, 176, 0.5);
  }
  .size-selection-row h4,
  .quantity-selection-row h4,
  .coffee-row h4 {
    font-size: 1rem;
    margin: 0.2rem 0;
    padding: 0.2rem;
    /* border: 1px solid red; */
  }
  .btn-row {
    padding: 0.2rem;
    /* border: 1px solid red; */
  }
  .btn-row .coffee-size {
    margin: 0 0.2rem;
    padding: 0.25rem 0.5rem;
    font-size: 1rem;
  }
  .btn-row .minus,
  .btn-row .plus {
    padding: 0.1rem;
    font-size: 1rem;
    /* border: 1px solid red; */
  }
  .btn-row span {
    font-size: 1rem;
  }

  .coffee-row-header p {
    font-size: 0.8rem;
    margin: 0.2rem 0;
    padding: 0.2rem;
  }

  .item-total {
    flex: 0 0 50px;
    /* padding: 0.25rem 1rem; */
    /* margin: 0 0.25rem; */
    font-size: 1.1rem;
    border: 1px solid red;
  }
}
